/**
 * HOME BLOCKS
 */

$base-block-height: 300px;

@mixin block-height ($height) {

    min-height: $height;

    //@media screen and (min-width: $menu-switch) { min-height: $height * 1.2;  }

    @media screen and (min-width: 900px) { min-height: $height * 1.75;  }
}

.wrap.wrap--home-blocks {
    position: relative;

    @media screen and (min-width: $desk-start)  { position: static; }
}

.home-blocks { @include outer-container(100%); }

.case-study {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-size: cover;
}

.block {
    background-size: cover;
    @include block-height($base-block-height);
}

/**
 * FIRST BLOCK
 */

.block--one {
    background-color: #fff;
    margin-top: -$spacing-unit--double*2;
    @include span-columns(12 of 12);

    @media screen and (min-width: $menu-switch) {
	@include span-columns(7);
    }

    @media screen and (min-width: 900px) {
	@include span-columns(6);
    }
}

.help-your-business {
    position: relative;
    padding: $spacing-unit--double;
    z-index: 999;

    @media screen and (min-width: $menu-switch) {
	padding: $spacing-unit * 2 $spacing-unit * 4 0 $spacing-unit;
    }

    @media screen and (min-width: 900px) {
	padding-left: 0;
	padding-top: $spacing-unit * 3;
    }
	
}

.help-your-business h2 {
    position: relative;
    z-index: 999; 
}

/**
 * SECOND BLOCK
 */

.block--two {
    @include outer-container(100%);
    //height: (-$spacing-unit--double*2) + $base-block-height * .75;	
    //@include block-height((-$spacing-unit--double*2) + $base-block-height * .75);
    @include span-columns(12, block-collapse);

    @media screen and (min-width: $menu-switch) { @include span-columns(5, block-collapse); }

    @media screen and (min-width: 900px) { @include span-columns(6, block-collapse); }

}

    .block--two__sub-block--one {
	position: relative;
	@include span-columns(12);
	@include block-height($base-block-height * .75);
	background-image: url(../images/sketch.png);
	z-index: 999;

	@media screen and (min-width: 900px) {
	    margin-top: -$spacing-unit--double*2;
	    @include span-columns(6);
	}
    }

    .block--two__sub-block--two {
	@include span-columns(12, block-collapse);
	@include arrow(top, ($spacing-unit * 1.5), #fff);
	padding: $spacing-unit;
	@include block-height((-$spacing-unit--double*2) + $base-block-height * .75);

	// arrow
	&:after {
	    position: absolute;
	    z-index: 999;
	    top: (-$spacing-unit * 1.5);  
	    right: $spacing-unit * 1.5;
	    margin: 0 auto;
	}

	@media screen and (min-width: 900px) {
	    padding: $spacing-unit--double;
	    @include span-columns(6, block-collapse);
	    @include arrow(left, ($spacing-unit * 1.5), #fff);

	    &:after {
		top: $spacing-unit*5; 
		margin: 0;
	    }
	}
    }

.mini-grid {
    @include outer-container(100%);
    margin-bottom: $spacing-unit * 4;
}

    .two-thirds {
	@include span-columns(8);
	padding-right: $spacing-unit--half;

	div {
	    height: 300px;
	    background-color: #ccc;
	}
	
    }

    .one-third { @include span-columns(4); }

    .one-whole div {
	height: (-$spacing-unit--half/2) + 150px;	
    }

    .one-whole:first-child {
	@include span-columns(12);
	padding-bottom: $spacing-unit--half;

	div:first-child {
	    background-color: #ccc;
	}
    }

    .one-whole:nth-child(2) {
	@include span-columns(12);
	padding-bottom: $spacing-unit--half;

	div:first-child {
	    background-color: #ccc;
	}
    }


/**
 * THIRD BLOCK
 */

.block--three {
    background-color: #fff;
    @include span-columns(12);

    @media screen and (min-width: $menu-switch) {
	@include span-columns(7);
    }

    @media screen and (min-width: 900px) {
	@include span-columns(6);
    }
}


.block--three__sub-block--one {
    @include span-columns(12, block-collapse);
    //@include block-height($base-block-height);
    //min-height: 450px;
    //@media screen and (min-width: $menu-switch) { min-height: $height * 1.2;  }
}

.houlden {
    background-image: url(../images/houlden.png);	
    position: static;
    margin-bottom: $spacing-unit;
    min-height: 300px;

    @media screen and (min-width: 900px) {
	min-height: 549px;
    }

    @media screen and (min-width: $desk-start) {
	min-height: 525px;
	position: absolute;
	left: 0;
	right: 50%;
	margin-bottom: 0;
    }

}

/**
 * FOURTH BLOCK
 */

.block--four {
    position: relative;
    //@include outer-container(100%);
    //height: (-$spacing-unit--double*2) + $base-block-height * .75;	
    //@include block-height((-$spacing-unit--double*2) + $base-block-height * .75);
    @include span-columns(12, block-collapse);
    z-index: 9999;

    @media screen and (min-width: $menu-switch) {
	@include span-columns(5, block-collapse);
	margin-top: -$base-block-height/2;
	padding-bottom: 100px;
    }

    @media screen and (min-width: 900px) {
	padding: 0;
	@include span-columns(6, block-collapse);
	margin-top: -$base-block-height + ($spacing-unit * 3.75);
    }

}

    .block--four__sub-block--one {
	background-image: url(../images/trespass.png);	
	//@include span-columns(12, block-collapse);
	//@include block-height($base-block-height);


		min-height: 450px;

		//@media screen and (min-width: $menu-switch) { min-height: $height * 1.2;  }

		@media screen and (min-width: 900px) { min-height: 508px;  }
    }

    .block--four__sub-block--two {
	//@include span-columns(12, block-collapse);
	@include block-height($base-block-height/2);
	position: relative;
    }

	.testimonial {
	    color: #fff;	
	    background-color: #444;
	    padding: $spacing-unit--double;
	    margin-top: $spacing-unit;
	    text-align: center;

	    @media screen and (min-width: $menu-switch) {
		position: absolute;
		//bottom: 0;
		left: -140%;
		//right: 0;
	//	font-size: $font-size * .85;	
		margin-top: 0;
		max-height: 670px;
	    }
	    
	    @media screen and (min-width: 900px) {
		max-height: 483px;
		left: 0;
	    }

	    @media screen and (min-width: $desk-start +100) {
		font-size: $font-size;	
	    }
}
	
	    .testimonial__title {
		margin-bottom: $spacing-unit * 3;
	    }

	    .testimonial__title .underline--short:after { background-color: #666; }

/**
 * FIFTH BLOCK
 */

.block--five,
.block--seven {
    position: relative;
    @include span-columns(12);	

    @media screen and (min-width: $menu-switch) {
	@include span-columns(3);	
    }

}

.case-study--five { background-image: url(../images/skypark.png);  }

.case-study--six { background-image: url(../images/simon-howie.png);  }

.block--six {
    @include span-columns(12);	

    @media screen and (min-width: $menu-switch) {
	@include span-columns(6);	
    }
    padding: $spacing-unit--double;
}

