/*-----------------------------*\

OBJECTS 

\*-----------------------------*/

/**
 * WRAP
 */

.wrap {
    position: relative;
    display: block;
    max-width: $wrap-width;
    padding: 0 $spacing-unit;
    margin: 0 auto;    
}

.wrap--full-height {
    height: 100%;
}

.wrap--static {
    position: static;
}

/**
 * TEXT
 */

.underline {
    padding-bottom: $spacing-unit/3;
    border-bottom: 1px #fff solid;
}

.underline--short {
    position: relative;	

    &:after {
	content: "";
	position: absolute;
	bottom: -$spacing-unit;
	left: 0;
	right: 0;
	margin: 0 auto;
	height: 2px;
	width: 45%;
	background-color: #fff;
    }
}

.lead {
    font-size: $font-size * 1.1;
}

a.highlight {
    color: $brand-color;
}

.em {
    font-style: italic;
}



/**
 * LISTS
 */

.stripped-list {
  list-style-type: none; 
}

.inline-list {
  text-align: center;  
}

.inline-list li {
  display: inline-block;
}
 
/**
 * ICONS 
 */

[class^="icon-"], [class*=" icon-"] {
    display: inline-block;
    font-size: $font-size;
}

/**
 * HERO BANNER
 */

.hero {
   height: 375px; 

   @include media($desk-up-break) {
	height: 450px; 
   }
}


