/**
 * BUTTONS
 */

.button--raquo {
    color: $brand-color;	
    background: none;
    border: 0;
    font-size: $font-size*1.75;
}

/**
 * PROFILE BLOCK 
 */

.profile {
    width: 275px;	
    height: 75px;
    margin: 0 auto;
}

.profile__photo {	
    float: left;
    width: 75px;
    height: 75px;
    font-size: 0;
}

.profile__photo img {
    width: 100%;
}

.profile__info {
    float: right;	
    text-align: left;
    line-height: $font-size * 1.3;
    @include vertical-align;
}

.profile__info__name {
    font-size: .9rem;	
    text-transform: uppercase;
    margin-bottom: 0;
}

.profile__info__title {
    margin-bottom: 0;
}
