/**
 * MAIN NAV 
 */

/**
 * MOBILE LINKS 
 */

.phone--mobile,
.main-nav-button {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 60px;
    display: block;
    text-align: center;

    @media screen and (min-width: $menu-switch) { 
	display: none;
    }
}

a.phone--mobile {
    display: block;
    right: 0; 
    height: 60px;   
    border-left: 1px solid #292929;
    text-align: center;

    @media screen and (min-width: $menu-switch) {
	display: none;
    }
}

    a.phone--mobile span {
	@include vertical-align;
	top: 45%;
    }

    a.phone--mobile span:before {
	font-size: $font-size*1.25;
    }

/**
 * NAV OPEN/CLOSE 
 */

.main-nav-button {
    height: 60px;
    left: 0;
    border-right: 1px solid #292929;
}

    .main-nav-button.active {
	background-color: $brand-color;
    }


/**
 * ANIMATED HAMBURGER ICON
 */

.hamburger {
    display: inline-block;
    width: $spacing-unit--double;
    height: $spacing-unit--double;
    z-index: 9999;
    @include transition(all 0.25s ease);
    @include vertical-align; 
}

    .hamburger__slice {
	margin-bottom: 5px;
	height: 2px;
	background: #fff;
	@include transition(all 0.25s ease);
    }

	.active .hamburger__slice--top { @include transform(translate3d(0,7px, 0) rotate(45deg)); }
	.active .hamburger__slice--mid { opacity: 0; }
	.active .hamburger__slice--btm { @include transform(translate3d(0,-7px, 0) rotate(-45deg)); }

/**
 * MAIN NAVIGATION
 */

.main-nav {
    border-top: 1px solid #292929;
    display: none;

    @media screen and (min-width: $menu-switch) {
	display: block;
    }
}

.main-nav.open {
    display: block;
}

    .main-nav ul {
	text-align: left;	
	padding: $spacing-unit 0;

	@media screen and (min-width: $menu-switch) {
	    text-align: center;	
	    padding: 0;
	}
    }

	.main-nav li {
	    position: relative;
	    margin-bottom: 0;
	    font-size: $font-size;
	    display: block;

	    @media screen and (min-width: $menu-switch) {
		display: inline-block;	
		font-size: $font-size*.75;
	    }

	    @media screen and (min-width: 900px) {
		font-size: $font-size;
	    }
	}

	.main-nav a {
	    display: block;
	    padding: $spacing-unit $spacing-unit--double;
	    text-transform: uppercase;

	    @media screen and (min-width: $menu-switch) {
		padding: $spacing-unit--double $spacing-unit;
	    }

	    @media screen and (min-width: 800px) {
		padding: $spacing-unit--double;
	    }
	}

	.main-nav .active {
	    position: relative; 
	}

	.main-nav li a:after,
	.main-nav .active a:after {
	    content: "";
	    position: absolute;
	    top: 0;
	    bottom: 0;
	    left: 0;
	    border-left: 5px solid $brand-color;

	    @media screen and (min-width: $menu-switch) {
		height: 5px;
		top: auto;
		left: 0; 
		right: 0;
		border-bottom: 5px solid $brand-color;
	    }
	    
	    @media screen and (min-width: 800px) {
		left: $spacing-unit--double;
		right: $spacing-unit--double;
	    }
	}

	li.main-nav__active a:after {
	    @include transform(translate3d(0, 0, 0));
	}

	.main-nav a:after {
	    @include transform(translate3d(-5px, 0, 0));
	    @include transition(transform .5s);

	    @media screen and (min-width: $menu-switch) { @include transform(translate3d(0, 5px, 0)); }
	}

	.main-nav li:hover a:after {
	    @include transform(translate3d(0, 0, 0));
	    @include transition(transform .5s);
	}

/**
 * SUB MENU
 */

.sub-menu {
    position: absolute;
}
