footer {
    background-color: $ui-color--dark;
    color: #fff;
    margin-top: $spacing-unit--double;
    padding: $spacing-unit--double*2 0;
    padding-bottom: 0;
}

.wrap--footer { @include outer-container($wrap-width); }

/**
 * FOOTER BLOCKS
 */

.footer-block {
    @include span-columns(6);	

    @include media($mobile) {
	@include span-columns(4);	
	padding-left: $spacing-unit--double;
    } 
}

    .footer-block__image {
	    display: block;
	    float: none;
	    padding-top: $spacing-unit--half;

	    @media screen and (min-width: $small-end) {
		margin: 0;
		float: left;
		width: 55px;
	    }
    }

    .footer-block__text {
	    font-size: $font-size * .8;

	    @media screen and (min-width: $desk-start) {
		font-size: $font-size;
	    }

	    @include media($small-break) {
		padding: 0 $spacing-unit;
		@include span-columns(4);	
	    } 
    }

	.footer-block h5 {
		font-size: $font-size;

		@media screen and (min-width: $desk-start) {
		    font-size: $font-size*1.25;
		}
	}

/**
 * HIRE ME BLOCK
 */

.hire-me {
    @include media($mobile) {
	margin-bottom: $spacing-unit--double*2;
    } 
}

    .hire-me__image {
	@extend .main-header__logo;	    
	margin: $spacing-unit--double auto;
	width: 65px; 
	height: 65px; 
    }

    .hire-me__text {
	padding: 0 $spacing-unit*3;
	padding-right: 0;
	@include span-columns(8);	
	
	@include media($mobile) {
	   // padding: 0 $spacing-unit*3;
	    @include span-columns(3);	
	} 
    }
    
	.hire-me__text p { color: #ccc; }

	.hire-me__text a {
	    font-size: $font-size * .85;	

	    @media screen and (min-width: $desk-start) { font-size: $font-size*1.05;	}
	}

/**
 * NEWSLETTER BLOCK
 */

.newsletter {
}

    .newsletter__image {
	margin: 0 auto;
	min-width: 100px;
	max-width: 195px;

	@media screen and (min-width: $menu-switch) { @include span-columns(4); }
    }

    .newsletter__text {
	@include span-columns(8);
	padding: 0 $spacing-unit--double;
    }

    .newsletter__text h5 { margin-bottom: 0; }

    .newsletter__text form {
	position: relative;
	max-width: 100%;
	
	//@media screen and (min-width: $menu-switch) { max-width: 310px; }
    }

    .newsletter__text .button--raquo {
	position: absolute;
	top: 0;
	right: $spacing-unit--half;

	@media screen and (min-width: $menu-switch) { top: 2px; }
    }
    
    .newsletter__text p {
	color: #ccc;
	margin: 0;
	margin-bottom: $spacing-unit--half;
    }

    .newsletter__image img { max-width: 100%; }

/**
 * FOOTER LINKS
 */

.nav--footer {
    @include outer-container(100%);	
    border-top: 1px solid #292929;
    border-bottom: 1px solid #292929;
    padding: $spacing-unit--double 0;
    margin-top: $spacing-unit--double;
}

.nav-list {

    @include media($menu-switch-break) {
	padding: 0 $spacing-unit;
	margin-top: $spacing-unit--double;
	@include span-columns(12);	
	@include omega(2n);
    }

    @include media($palm-break) {
	margin-top: $spacing-unit--double;
	padding: 0 $spacing-unit;
	@include span-columns(6);	
	@include omega(2n);
    }

    @include media($palm-break) {
	margin-top: $spacing-unit--double;
	padding: 0 $spacing-unit;
	@include span-columns(6);	
	@include omega(2n);
    }

    @include media($lap-up-break) { @include span-columns(3); }

}

    .nav-list h5 {
	    font-size: $font-size;
	    margin-bottom: $spacing-unit * 2.75;

	    @include media($menu-switch-break) { margin-bottom: $spacing-unit--double; }

	    @include media($lap-break) { margin-bottom: $spacing-unit--half; }

	    @media screen and (min-width: $desk-start) { font-size: $font-size*1.25; }

    }

    .nav-list ul { @extend .stripped-list; }

	.nav-list li { margin: 0; }

	    .nav-list a { color: #ccc; }


/**
 * SOCIAL LINKS
 */

.nav-list--four li {
    display: inline-block;	
    margin-right: $spacing-unit--half;
}

    .nav-list--four span {
	color: $brand-color;
	font-size: $font-size*2; 
    }

    .nav-list--four a {
    	font-size: 0;
    }

    span.icon-twitter { font-size: $font-size*2; }

    span.icon-pinterest { font-size: $font-size*2.2; }

/**
 * COPYRIGHT
 */

.copyright {
	text-align: center;
	margin: $spacing-unit--double 0;
	color: #ccc;
}
