/**
 * HOME HERO BANNER
 */

.hero--home {
    position: relative;
    background-color: #6090a6;
    overflow: hidden;
}

// Mask
.hero--home:after {
    position: absolute;
    left: 0;
    bottom: 0;
    height: $spacing-unit--double*2;
    width: 50%;
    background-color: #fff;

    @media screen and (min-width: $menu-switch) {
	content: "";
    }
}

.hero__text--home {
    color: #fff;	
    text-transform: uppercase;
    max-width: 165px;
    margin: $spacing-unit--double auto;
    text-align: center;

    @media screen and (min-width: $menu-switch) {
	text-align: left;
	margin: 0;
	@include vertical-align;
    }
}

.hero--home__item--two, 
.hero--home__item {	
}

.hero--home__item {
    margin: 0 auto;
    max-width: 450px;

    @media screen and (min-width: $menu-switch) {
	position: absolute;
	bottom: -$spacing-unit*2;
	right: -$spacing-unit--half;
	max-width: 445px;
    }

    @include media(670px) {
	max-width: 500px;
    }
    
    @include media($desk-up-break) {
	max-width: 611px;
    }
}

.hero--home__item--two {
    display: none;
    position: absolute;
    bottom: -$spacing-unit;
    @include outer-container(100%);

    @media screen and (min-width: 750px) {
	display: block;
	right: 45%;
	max-width: 250px;
    }

    @include media($desk-up-break) {
	right: $spacing-unit*55;
	max-width: 100%;
    }
}

.hero--home__item--two img,
.hero--home__item img {
    max-width: 100%;
}

