/*-----------------------------*\
  
BASE

\*-----------------------------*/


body {
    font-family: $brand-font;
    font-size: $font-size;
    line-height: $font-size * 1.75; 
    color: $font-color;
}

p {
    margin-top: 0;
}

h1,
h2,
h3,
h4,
h5 {
    margin: 0 0 $spacing-unit 0;
    font-weight: normal;
}

ul {
    margin: 0;
    padding: 0;
}

li {
    margin-bottom: $spacing-unit/2;
}

a:link {
    color: #fff;
    text-decoration: none;
    -webkit-transition: color, .25s;
    transition: color, .25s;
}

a:hover,
a:visited,
a:focus,
a:active {
    text-decoration: none;
    color: #fff;
    -webkit-transition: color, .25s;
    transition: color, .25s;
}

form {
    position: relative;
}

input {
    padding: $spacing-unit;    	
    width: 100%;
    border-radius: 0;
    border: 0;
}
